/**
 * formstate
 * state
 * - field
 * - id id
 * - name   company.name  company["name"]  name
 * - groupName   company.name  company["name"]  name
 * - groupIndex Index
 * - label 
 * - rule 
 * - interceptor 
 * - noTrim 
 * - fieldRef 
 * */
import clone from 'lodash/clone';
import ruleValidate from './ruleValidate';

class Field {
  constructor({ id, name }) {
    this.id = id;
    this.name = name;
    this.isPass = false;
  }

  setInfo({ groupName, groupIndex, label, rule, interceptor, noTrim, fieldRef }) {
    this.groupName = groupName;
    this.groupIndex = groupIndex;
    this.label = label;
    this.rule = rule;
    this.interceptor = interceptor;
    this.noTrim = noTrim;
    this.fieldRef = fieldRef;
    return this;
  }

  deleteValue() {
    delete this.value;
    return this;
  }

  setValue(value) {
    this.value = value;
    return this;
  }

  clone() {
    return clone(this);
  }

  setValidateStatus({ status, msg = '' }) {
    this.validate = {
      status,
      msg
    };
    return this;
  }

  async runValidate(rules, getFormData) {
    const validate = await ruleValidate({
      field: {
        name: this.name,
        rule: this.rule
      },
      value: this.value,
      formRules: rules,
      getFormData
    });
    this.isPass = validate.result;
    this.validate = {
      status: validate.result === true ? 1 : 2,
      msg: validate.errMsg
    };
    return this;
  }
}

export default Field;
